.back {
    border: 2px solid black; /* Borde negro */
    background-color: transparent; /* Fondo transparente */
    color: black; /* Color de texto negro por defecto */
    padding: 10px 20px; /* Ajusta el padding seg煤n tus necesidades */
    transition: background-color 0.3s, color 0.3s; /* Transici贸n suave */
    display: block; /* Para que el bot贸n ocupe el ancho m谩ximo disponible */
    margin: 0 auto; /* Para centrar el bot贸n horizontalmente */
    text-align: center; /* Para centrar el texto dentro del bot贸n */
}
.back:hover {
    background-color: black; /* Cambia el fondo a negro al pasar el cursor */
    color: white; /* Cambia el color del texto a blanco al pasar el cursor */
}
.timer{
    display: block; /* Para que el bot贸n ocupe el ancho m谩ximo disponible */
    margin: 0 auto; /* Para centrar el bot贸n horizontalmente */
    text-align: center; /* Para centrar el texto dentro del bot贸n */
    margin-bottom: 2%; /* Para dejar un margen debajo del timer */
}







